@import url(https://fonts.googleapis.com/css?family=Roboto&subset=cyrillic);
#app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

a {
  text-decoration: none;
}
.icon {
  display: inline-block;
  background-repeat: no-repeat;
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
  cursor: pointer;
}
.icon:hover {
    opacity: .5;
}
.icon-delete {
    width: 24px;
    height: 24px;
    background-image: url(data:image/png;base64,);
}
.hello {
  width: 2000px;
}
.input {
  border: 4px solid #d7d7d7;
  border-radius: 8px;
  padding: 8px 16px;
  width: 400px;
  font-size: 32px;
  display: inline-block;
  text-align: center;
  outline: none;
}
.input--xs {
    width: 67px;
}
.web {
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.web__left {
    max-width: 1380px;
    -webkit-box-flex: 0;
        -ms-flex: 0 0 1380px;
            flex: 0 0 1380px;
}
.web__right {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.web__button {
    color: #e37458;
    font-size: 32px;
    border-bottom: 2px dotted;
    position: relative;
    padding-bottom: 2px;
    margin-left: 159px;
    margin-top: 24px;
    display: inline-block;
}
.web__button:before {
      content: '+';
      position: absolute;
      left: -20px;
}
.web__button:hover {
      color: #b63d1f;
}
.web__header {
    padding: 35px 0 22px 0;
    border-bottom: 6px solid #f1b16b;
    margin-bottom: 30px;
}
.web__header h1 {
      margin: 0;
      font-size: 48px;
      padding-left: 65px;
      font-weight: normal;
}
.web__content--headers {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 53px;
}
.web__content--header {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.4);
}
.web__form--color {
    max-width: 405px;
    -webkit-box-flex: 0;
        -ms-flex: 0 0 405px;
            flex: 0 0 405px;
    padding-left: 65px;
}
.web__form--shade {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 514px;
            flex: 0 0 514px;
    max-width: 514px;
}
.web__form--scope {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 185px;
            flex: 0 0 185px;
    max-width: 185px;
}
.web__form--pack {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 180px;
            flex: 0 0 180px;
    max-width: 180px;
}
.web__form--func {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 25px;
            flex: 0 0 25px;
    max-width: 25px;
}
.web__form--header {
    font-size: 24px;
    color: rgba(0, 0, 0, 0.4);
    margin-bottom: 65px;
    /*margin-left: 70px*/
}
.web__form--content {
    padding: 25px 0 25px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.web__form--content_group {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.web__form--content_groups {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.web__form--content_item {
      margin-bottom: 45px;
      font-size: 24px;
}
.web__form--content_item--scope {
        margin-left: 13px;
}
.web__form--content_item--pack {
        font-size: 32px;
}
.web__form--content_item--color {
        border-bottom: 2px solid rgba(0, 0, 0, 0.15);
        font-size: 36px;
        /*margin-left: 70px*/
}

.checkbox[data-v-2ab2fa5e]{
    display: inline-block;
    position: relative;
    margin-right: 25px;
    width: 28px;
    height: 28px;
    border: 4px solid #d7d7d7;
    border-radius: 8px;
    vertical-align: middle;
}
.checkbox input[data-v-2ab2fa5e]{
    display: none;
}
.checkmark[data-v-2ab2fa5e]{
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(data:image/png;base64,) no-repeat center;
    opacity: 0;
}
.checkbox input:checked ~ .checkmark[data-v-2ab2fa5e] {
   opacity: 1;
}
